/* Gutenberg CSS that will be loaded on the frontend of Gutenberg enabled single posts */

@import 'variables';

:root{
    --containerSingleWidth: #{$container-single};
}

.single-content-section{
    .entry-content-wrapper{
        @media only screen and (min-width: 768px) and (max-width: calc( #{$container-single} + 100px )){
            padding-left: 50px;
            padding-right: 50px;
        }
    }
    .wp-block-image{
        img{
        }
        figcaption{
        }
    }
    .wp-block-separator{
        &:not(.alignfull){
            max-width: #{$container-single};
        }
    }
}